@import '../../assets/style/variables.scss';
.dark_mode {
    margin-top: -20px;
    padding: 0 $size-1;
}
.dark_mode_label {
    width: 65px;
    height: 30px;
    position: relative;
    display: block;
    background: #ebebeb;
    border-radius: 200px;
    box-shadow: inset 0 5px 15px rgba(0, 0, 0, 0.4), inset 0 -5px 15px rgba(255, 255, 255, 0.4);
    cursor: pointer;
    transition: $transition-03;
}
.dark_mode_label:after {
    content: "";
    width: 25px;
    height: 25px;
    position: absolute;
    top: 3px;
    left: 3px;
    background: linear-gradient(180deg, #ffcc89, #d8860b);
    border-radius: 180px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    transition: $transition-03;
}
.dark_mode_input {
    width: 0;
    height: 0;
    visibility: hidden;
}
.dark_mode_input:checked + .dark_mode_label {
    background: #242424;
}
.dark_mode_input:checked + .dark_mode_label:after {
    left: 62px;
    transform: translateX(-100%);
    background: linear-gradient(180deg, #777, #3a3a3a);
}
.dark_mode_label:active:after {
    width: 30px;
}
.dark_mode_label i {
    position: absolute;
    width: 20px;
    top: 7px;
    z-index: 100;
}
.dark_mode_label i.icon-Sun {
    left: 7px;
    fill: #fff;
    transition: $transition-03;
}
.dark_mode_label i.icon-Moon {
    left: 43px;
    fill: #7e7e7e;
    transition: $transition-03;
}
.dark_mode_input:checked + .dark_mode_label i.icon-Sun {
    fill: #7e7e7e;
}
.dark_mode_input:checked + .dark_mode_label i.icon-Moon {
    fill: #fff;
}